html {
  font-size: 62.5%; }

body {
  font-family: "Open Sans", sans-serif; }

.wrapper {
  background-image: url(../img/oak.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1; }

.header__wrapper {
  position: relative;
  overflow: hidden; }

.triangle {
  height: 70rem;
  background: linear-gradient(to right, #00a0de 0%, #00a0de 50%, #1390c1 50%, #1390c1 100%);
  position: relative; }

.triangle__content {
  width: 100%; }

.triangle-arrow {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding-bottom: 24%;
  background-color: #fff; }

.triangle-arrow:before,
.triangle-arrow:after {
  content: '';
  position: absolute;
  bottom: 100%;
  width: 50%;
  padding-bottom: inherit;
  background-color: inherit; }

.triangle-arrow:before {
  right: 50%;
  transform-origin: 100% 100%;
  transform: skewX(66deg); }

.triangle-arrow:after {
  left: 50%;
  transform-origin: 0 100%;
  transform: skewX(-66deg); }

.header__navigation {
  display: flex;
  justify-content: space-around;
  width: 65%;
  margin: 0 auto; }

.header__navigation-link {
  display: inline-block;
  width: 20%;
  font-size: 2.1rem;
  border: 0.05em solid #fff;
  border-top: none;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: #fff; }

.logo img {
  top: 10rem;
  z-index: 2; }

.main img {
  width: 100%; }
